*{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.table{
    display: flex;
    justify-content: left;
}
img{
    max-width: 200px;
    max-height: 2%;
    margin: 0;
    padding: 0;
 }
 table{
     table-layout: auto;
     margin-left: auto;
     margin-right: auto;
 }
.product{
    display: flex;
    justify-content: first baseline;
    align-items: center;
}

thead{
    background-image: linear-gradient(
        to bottom,
        whitesmoke,#ffe,grey
    );
}
th,td{
   border :1px solid gray
}
.quantity{
    display: flex;
    justify-content: first baseline;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}
button.control{
    background-color: #0ff;
    border-radius: 20px;
    border: 0;
}
button.control:active{
   
    background-color: rgb(0, 40, 255);
}
button.cancel{
    height: 40px;
    border-radius: 20px;
    background-color: lightcoral;
    border: 0;
}
button.cancel:active{
    background-color: rgb(255,0, 0);
}

@media screen and (min-width:1000px){
    /* .product{
      margin-left: 20%;
        
    } */
}
div.nav{
    display: block;
    width: auto;
    max-height: 100px;
    background-color: blue;
}
div.nav>h2{
    color: whitesmoke;

}
.buy{
    margin-left: auto;
    margin-right: auto;
    border: 0;
    border-radius: 2px;
    background-color:rgba(0,255 ,0);
}
.buy:active{
    background-color: rgba(0,255 ,1,0.5);
}
